:root {
  --linkcolor: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  justify-content: flex-start;
  align-items: center;
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  line-height: normal;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--linkcolor);
}

.nav__item {
  margin-right: 20px;
  position: relative;
}

.nav__item:last-child {
  margin-right: 0;
}

.is-active {
  color: var(--linkcolor);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--linkcolor);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

